<template>
  <div class="login_box">
        <div class="header">
          <div class="logo"><!-- {{this.$store.state.area}} -->智慧校园动态监控平台</div>
          <clock></clock>
        </div>
        <div data-v-ce3c736e="" class="bgLight"></div>
        <div class="login_l_img"><img src="./images/login-img.png" /></div>
        <div class="login">
            <div class="login_logo"><a href="#"><img src="./images/login_logo.png" /></a></div>
            <div class="login_name">
                 <p>登录</p>
            </div>
              <input type="text"
                v-model="name"
                @keyup.enter="checkVal"
                onfocus="if(this.value=='用户名'){this.value=''}"
                onblur="if(this.value==''){this.value='用户名'}"
              />
              <input
                v-model="pwd"
                @keyup.enter="checkVal" type="password" id="password"
                onfocus="document.getElementById('password_text').style.display='none';"
                onblur="if(this.value==''){document.getElementById('password_text').style.display='block';};"
              />
              <span id="password_text" v-show="pwd === ''"
                onclick="this.style.display='none';document.getElementById('password').focus()"
              >密码
              </span>
              <input type="button" @click="checkVal" value="登录" style="width:100%;" />
              <p class="error">{{error}}</p>
        </div>
        <div class="copyright">杭州碧海银帆网络技术有限公司  浙ICP备07034131号 技术支持电话：400-600-4501</div>
  </div>

</template>

<script>
import axios from 'axios'
import Clock from '../home/index/components/Clock'
export default {
  name: 'Login',
  components: {
    Clock
  },
  data () {
    return {
      name: '用户名',
      pwd: '',
      error: ''
    }
  },
  mounted() {
    // this.$store.dispatch('changeArea', '青秀区')
  },
  methods: {
    checkVal () {
      if (this.name === '' || this.name === '用户名') {
        this.error = '用户名不能为空'
        return false
      } else if (this.pwd === '') {
        this.error = '密码不能为空'
        return false
      } else {
        this.error = ''
        this.isLogin()
      }
    },
    isLogin () {
      axios.get('/static/data/test.json').then((res) => {
        // console.log(res)
        const users = res.data.users
        const thisUser = users.filter((p) => {
          return p.username === this.name
        })
        if (thisUser.length === 0) {
          this.error = '用户不存在'
          return
        } else if (thisUser[0].password !== this.pwd) {
          this.error = '密码错误'
          return
        } else {
          // this.$store.commit('changeAreaLevel', '园级')
          this.$store.commit('changeLogin', '100')
          localStorage.setItem('isLogin', this.$store.state.isLogin)
          if (this.$store.state.areaLevel !== '园级') {
            this.$router.push({name: 'Index'})
          } else {
            this.$router.push({name: 'GIndex'})
          }
        }
      }).catch((e) => {
        this.error = e
      })
    }
  }
}
</script>

<style scoped>
* { font: 13px/1.5 '微软雅黑'; -webkit-box-sizing: border-box; -moz-box-sizing: border-box; -box-sizing: border-box; padding:0; margin:0; list-style:none; box-sizing: border-box; }
body, html { height:100%; overflow:hidden; }
a { color:#27A9E3; text-decoration:none; cursor:pointer; }
img{ border:none;}

.login_box{ width:100%; height: 100%;overflow: hidden;background: url(../../assets/images/bg_skin.jpg);}
.login_box .login_l_img{position: absolute;left: 50%;top: 50%;transform: translate(-50%, -50%);margin-left: -300px;}
.login_box .login_l_img img{ width:600px; }
.login {position: absolute;left: 50%;top: 50%;transform: translate(-50%, -50%);margin-left: 400px;width:300px; padding:20px 30px 50px; background-color: #ffffff;border-radius:6px;box-sizing: border-box;}
.login_logo{ display: none;width:120px; height:120px; border:5px solid #93defe;border-radius:100px; background:#fff; text-align:center; line-height:110px; position:absolute; top:-60px; right:140px;}
.login_name{ width:100%; float:left;}
.login_name p{ width:100%;  font-size:20px; color:#444; padding:10px 0 35px;font-weight: bold;}
.login_logo img{ width:60px; height:60px;display: inline-block; vertical-align: middle;}
input[type=text], input[type=file], input[type=password], input[type=email], select { background: #efefef!important; vertical-align: middle; border-radius: 3px; height: 50px; padding: 0px 16px; font-size: 14px; color: #555555; outline:none; width:100%;margin-bottom: 30px;line-height:50px; color:#888;}
input[type=text]:focus, input[type=file]:focus, input[type=password]:focus, input[type=email]:focus, select:focus { border: 1px solid #27A9E3; }
input[type=submit], input[type=button] { display: inline-block; vertical-align: middle; padding: 12px 24px; margin: 0px; font-size:16px; line-height: 24px; text-align: center; white-space: nowrap; vertical-align: middle; cursor: pointer; color: #ffffff; background: linear-gradient(to right, #542cff , #369cff); border-radius: 3px; border: none; -webkit-appearance: none; outline:none; width:100%; }
.copyright { position: absolute;left: 0;bottom: 30px;font-size:14px; color:#5b6c95; display:block;width:100%; text-align:center;}

#password_text{vertical-align: middle; border-radius: 3px; height: 50px;padding: 0px 16px; font-size: 14px; color: #888; outline:none; width:100%;margin-top: -80px;display: block; line-height:50px; margin-bottom: 30px;position: relative; z-index: 9;}
.error{text-align: center;line-height: 40px;}
</style>
<style lang="stylus" scoped>
  .header
    display: flex
    background: linear-gradient(to bottom, rgba(33, 90, 144, 1) 0%, rgba(33, 90, 144, 0) 100%)
    height: 75px
    box-shadow: 0 -1px 6px #2779c7 inset
    padding: 0 20px;
    .logo
      flex: 1
      color: #fff
      font-size: 28px
      font-family: '方正正准黑简体常规'
      background: linear-gradient(to bottom, #fff 0%, #fff 40%, #a5b8ec 50%, #fff 60%, #fff 100%)
      -webkit-background-clip: text
      -webkit-text-fill-color: transparent
    .logo:before, .logo:after
      content: ''
      display:inline-block
      vertical-align: middle
      width: 80px
      height: 75px
      background: url(../../assets/images/logo_l.png) center no-repeat
    .logo:after
      background-image: url(../../assets/images/logo_r.png)
  .bgLight
    position: fixed
    left: 0
    top: 50%
    margin: -500px 0 0 100px
    width: 800px
    height: 800px
    border-radius: 50%
    background: radial-gradient(rgba(108, 145, 211, .3), rgba(22, 45, 85, 0), rgba(22, 45, 85, 0))
</style>



