  cityMCheck: '/temperatures/invoke/citygraph', // 市级各县晨检记录 aeCheckDay={aeCheckDay}&bCheckDay={bCheckDay}&cityName={cityName}
  districtMCheck: '/temperatures/invoke/districtgraph', // 县级各园晨检记录 aeCheckDay={aeCheckDay}&bCheckDay={bCheckDay}&districtName={districtName}
