  monthprovincecheck: '/attenddemo/invoke/monthprovincecheck', // 该省内各园区以市为单位的晨检统计 aeMonth={aeMonth}&bMonth={bMonth}&provinceName={provinceName}
  monthcitycheck: '/attenddemo/invoke/monthcitycheck', // 该市内各园区以区为单位的晨检统计 aeMonth={aeMonth}&bMonth={bMonth}&cityName={cityName}
  monthdistrictcheck: '/attenddemo/invoke/monthdistrictcheck', // 该区内以各园区为单位的晨检统计 aeMonth={aeMonth}&bMonth={bMonth}&districtName={districtName}
