  sportCompare: '/piecedemo/invoke/sportCompare', // 省、市、县 一段时间内，平均睡眠时长和各种睡眠状态的人数 gardeNumber={gardeNumber}&firstDateTime={firstDateTime}&lastDateTime={lastDateTime}&provinceName={provinceName}&cityName={cityName}&districtName={districtName}
  singleAreaSleepCompare: '/piecedemo/invoke/singleAreaSleepCompare', // 一个市或区/县某个时间段内每个月份的各种睡眠状态的人数 gardeNumber={gardeNumber}&firstDateTime={firstDateTime}&lastDateTime={lastDateTime}&cityName={cityName}&districtName={districtName}
