  getSleepState: '/piecedemo/invoke/gradeNumberSleep', // 园所睡眠状态
  getSleepStateCount: '/piecedemo/invoke/gradeSleepTypeNumber', // 园所睡眠状态个数
  getSportStateCount: '/piecedemo/invoke/gradeSportTypeNumber', // 园所运动状态个数
  getSportState: '/piecedemo/invoke/gradeNumberSport' // 园所运动状态
