export default {
  // 边框样式
  border (...args) {
    window.$.each(args, (index, value) => {
      const $obj = value
      $obj.css({'position': 'relative'})
      const presetCss = [{
        top: '0',
        left: '0',
        margin: '-2px 0 0 -2px',
        'border-color': '#b3f8ff',
        'border-width': '2px 0 0 2px',
        'border-top-left-radius': '3px'
      },
      {
        top: '0',
        right: '0',
        margin: '-2px -2px 0 0',
        'border-cColor': '#b3f8ff',
        'border-width': '2px 2px 0 0',
        'border-top-right-radius': '3px'
      },
      {
        bottom: '0',
        right: '0',
        margin: '0 -2px -2px 0',
        'border-color': '#b3f8ff',
        'border-width': '0 2px 2px 0',
        'border-bottom-right-radius': '3px'
      },
      {
        bottom: '0',
        left: '0',
        margin: '0 0 -2px -2px',
        'border-color': '#b3f8ff',
        'border-width': '0 0 2px 2px',
        'border-bottom-left-radius': '3px'
      }]
      let angels = '<div class="angels">'
      for (let i = 0; i < 4; i++) {
        angels += '<b style="position:absolute;width:30px;height:30px;border-style:solid;'
        for (let j in presetCss[i]) {
          angels += j + ':' + presetCss[i][j] + ';'
        }
        angels += '"></b>'
      }
      angels += '</div>'
      $obj.append(angels)
    })
  },
  // echarts屏幕缩放
  winResize (obj) {
    let resizeTimer = null
    window.addEventListener('resize', () => {
      if (resizeTimer) clearTimeout(resizeTimer)
      resizeTimer = setTimeout(() => {
        obj.resize()
      }, 500)
    })
  },
  // 去重
  excludeRepeat (oldArr, repeatKey) {
    const newArr = []
    for (let i = 0; i < oldArr.length; i++) {
      let flag = true
      for (let j = 0; j < newArr.length; j++) {
        if (oldArr[i][repeatKey] === newArr[j]) {
          flag = false
        }
      }
      if (flag) {
        newArr.push(oldArr[i][repeatKey])
      }
    }
    return newArr
  },
  // 计算所有相同属性数量
  compare ($obj, $key) {
    let compareObj = {}
    for (let i = 0, len = $obj.length; i < len; i++) {
      if ($obj[i][$key] !== '') {
        if (compareObj[$obj[i][$key]] === undefined) {
          compareObj[$obj[i][$key]] = 1
        } else {
          compareObj[$obj[i][$key]] = compareObj[$obj[i][$key]] + 1
        }
      }
    }
    return compareObj
  },
  // 排序
  compareSort ($arr, key, Asc) {
    // 根据属性排序
    function compareFunc (propertyName) {
      if (Asc) {
        return function (obj1, obj2) {
          var val1, val2
          val1 = obj1[propertyName]
          val2 = obj2[propertyName]
          if (val1 < val2) {
            return -1
          } else if (val1 > val2) {
            return 1
          } else {
            return 0
          }
        }
      } else {
        return function (obj1, obj2) {
          var val1, val2
          val1 = obj1[propertyName]
          val2 = obj2[propertyName]
          if (val1 < val2) {
            return 1
          } else if (val1 > val2) {
            return -1
          } else {
            return 0
          }
        }
      }
    }
    const copy = $arr.slice(0)
    return copy.sort(compareFunc(key))
  }
}



