import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/pages/home/index/Index'
import Login from '@/pages/login/Login'
import NavList from './navList'
import GNavList from './gNavList'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: {
        name: 'Login'
      }
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/index',
      name: 'Index',
      meta: {
        requireAuth: true
      },
      component: Index,
      redirect: {
        name: 'HealthyToday'
      },
      children: NavList
    },
    {
      path: '/index',
      name: 'GIndex',
      meta: {
        requireAuth: true
      },
      component: Index,
      redirect: {
        name: 'GHealthyToday'
      },
      children: GNavList
    }
  ]
})



