import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    area: '浙江省',
    isLogin: '0',
    mapArea: {
      level: '省级',
      name: '浙江省',
      // ['榆林市', '安康市', '西安市', '商洛市', '铜川市', '咸阳市', '延安市', '汉中市', '宝鸡市', '渭南市']
      list: ['杭州市', '宁波市', '温州市', '嘉兴市', '湖州市', '绍兴市', '金华市', '衢州市', '舟山市', '丽水市']
    },
    areaList: ['杭州市', '宁波市', '温州市', '嘉兴市', '湖州市', '绍兴市', '金华市', '衢州市', '舟山市', '丽水市'],
    areaLevel: '县级',
    // 地图面包屑导航
    crumbs: {
      province: '广西壮族自治区',
      city: '南宁市',
      district: '青秀区',
      garden: '拱墅区第一幼儿园'
    },
    // 地区id列表
    areaIdList: [],
    // 园所id列表
    gardenIdList: []
  },
  actions: {
    changeArea (ctx, area) {
      ctx.commit('changeArea', area)
    },
    changeLogin (ctx, isLogin) {
      ctx.commit('changeLogin', isLogin)
    },
    changeMapArea (ctx, name) {
      ctx.commit('changeMapArea', name)
    },
    changeMapLevel (ctx, level) {
      ctx.commit('changeMapLevel', level)
    },
    changeMapList (ctx, list) {
      ctx.commit('changeMapList', list)
    },
    changeAreaList (ctx, areaList) {
      ctx.commit('changeAreaList', areaList)
    },
    changeAreaLevel (ctx, areaLevel) {
      ctx.commit('changeAreaLevel', areaLevel)
    },
    // 地图面包屑导航
    crumbsProvince ($obj, value) {
      $obj.commit('crumbsProvince', value)
    },
    crumbsCity ($obj, value) {
      $obj.commit('crumbsCity', value)
    },
    crumbsDistrict ($obj, value) {
      $obj.commit('crumbsDistrict', value)
    },
    crumbsGarden ($obj, value) {
      $obj.commit('crumbsGarden', value)
    },
    changeAreaIdList (ctx, areaIdList) {
      ctx.commit('changeAreaIdList', areaIdList)
    },
    changeGardenIdList (ctx, gardenIdList) {
      ctx.commit('changeGardenIdList', gardenIdList)
    }
  },
  mutations: {
    changeArea (state, area) {
      state.area = area
    },
    changeLogin (state, isLogin) {
      state.isLogin = isLogin
    },
    changeMapArea (state, name) {
      state.mapArea.name = name
    },
    changeMapLevel (state, level) {
      state.mapArea.level = level
    },
    changeMapList (state, list) {
      state.mapArea.list = list
    },
    changeAreaList (state, areaList) {
      state.areaList = areaList
    },
    changeAreaLevel (state, areaLevel) {
      state.areaLevel = areaLevel
    },
    // 地图面包屑导航
    crumbsProvince (state, value) {
      state.crumbs.province = value
    },
    crumbsCity (state, value) {
      state.crumbs.city = value
    },
    crumbsDistrict (state, value) {
      state.crumbs.district = value
    },
    crumbsGarden (state, value) {
      state.crumbs.garden = value
    },
    changeAreaIdList (state, areaIdList) {
      state.areaIdList = areaIdList
    },
    changeGardenIdList (state, gardenIdList) {
      state.gardenIdList = gardenIdList
    }
  }
})



