export default
[
  {
    path: '/healthy/today',
    name: 'HealthyToday',
    component: () =>
      import('@/pages/home/healthy/today/Today')
  },
  {
    path: '/healthy/contrast',
    name: 'HealthyContrast',
    component: () =>
      import('@/pages/home/healthy/contrast/Contrast')
  },
  {
    path: '/growth/today',
    name: 'GrowthToday',
    component: () =>
      import('@/pages/home/growth/today/Today')
  },
  {
    path: '/growth/contrast',
    name: 'GrowthContrast',
    component: () =>
      import('@/pages/home/growth/contrast/Contrast')
  },
  {
    path: '/activity/today',
    name: 'ActivityToday',
    component: () =>
      import('@/pages/home/activity/today/Today')
  },
  {
    path: '/activity/contrast',
    name: 'ActivityContrast',
    component: () =>
      import('@/pages/home/activity/contrast/Contrast')
  }
]



