import Vue from 'vue'
import 'element-ui/lib/theme-chalk/index.css'
import {CheckboxGroup, CheckboxButton, RadioGroup, RadioButton, DatePicker, MessageBox, Button, Message, Loading} from 'element-ui'

Vue.use(CheckboxGroup)
Vue.use(CheckboxButton)
Vue.use(DatePicker)
Vue.use(RadioGroup)
Vue.use(RadioButton)
// Vue.use(MessageBox)
// Vue.use(Message)
Vue.use(Button)
Vue.use(Loading.directive)
Vue.prototype.$loading = Loading.service
Vue.prototype.$prompt = MessageBox.prompt
Vue.prototype.$message = Message



