export default
[
  {
    path: '/garden/healthy/today',
    name: 'GHealthyToday',
    component: () =>
      import('@/pages/garden/healthy/today/Today')
  },
  {
    path: '/garden/healthy/contrast',
    name: 'GHealthyContrast',
    component: () =>
      import('@/pages/garden/healthy/contrast/Contrast')
  },
  {
    path: '/garden/growth/today',
    name: 'GGrowthToday',
    component: () =>
      import('@/pages/garden/growth/today/Today')
  },
  {
    path: '/garden/growth/contrast',
    name: 'GGrowthContrast',
    component: () =>
      import('@/pages/garden/growth/contrast/Contrast')
  },
  {
    path: '/garden/activity/today',
    name: 'GActivityToday',
    component: () =>
      import('@/pages/garden/activity/today/Today')
  },
  {
    path: '/garden/activity/contrast',
    name: 'GActivityContrast',
    component: () =>
      import('@/pages/garden/activity/contrast/Contrast')
  }
]



